<template>
  <div>
    <h2>产品管理</h2>
    <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <!-- <div style="width: 100%;">
          <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
          <a-button type="primary">搜索</a-button>
        </div> -->
        <a-button type="primary" @click="popProduct">添加产品</a-button>
      </a-col>
    </a-row>
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="toEdit(text.id)">编辑</a-button>
        <a-button type="link" @click="toDelete(text.id)">删除</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="productVisible"
      :title="isEdit ? '编辑产品':'添加产品'"
      width="60%"
      @ok="handleSubmit"
      :okButtonProps="{
        props: {
          loading: submitLoading
        }
      }"
    >
      <a-spin :spinning="modalLoading">
        <a-form>
          <a-form-item label="产品名称">
            <a-input v-model="form.name" />
          </a-form-item>
          <a-form-item label="介绍">
            <a-input v-model="form.introduction" />
          </a-form-item>
          <a-form-item label="产品封面">
            <a-upload
              name="file"
              list-type="picture-card"
              class="avatar-uploader"
              :show-upload-list="false"
              :action="`${uploadPrefix}/upload`"
              @change="hanldeCoverChange"
            >
              <div
                v-if="form.cover_img"
                class="bg_img cover_img"
                :style="`background-image: url(${imgPrefix}${form.cover_img});`"
              ></div>
              <div v-else>
                <a-icon :type="coverLoading ? 'loading' : 'plus'" />
                <div class="ant-upload-text">
                  Upload
                </div>
              </div>
            </a-upload>
          </a-form-item>
          <a-form-item label="产品详情图">
            <a-upload
              name="file"
              list-type="picture-card"
              class="avatar-uploader"
              :show-upload-list="false"
              :action="`${uploadPrefix}/upload`"
              @change="hanldeDetailChange"
            >
              <div
                v-if="form.detail_img"
                class="bg_img cover_img"
                :style="`background-image: url(${imgPrefix}${form.detail_img});`"
              ></div>
              <div v-else>
                <a-icon :type="detailLoading ? 'loading' : 'plus'" />
                <div class="ant-upload-text">
                  Upload
                </div>
              </div>
            </a-upload>
          </a-form-item>
          <a-form-item label="所属分类">
            <a-select 
              v-model="form.category_id" 
              :options="category"
            />
          </a-form-item>
          <a-form-item label="数量" type="number">
            <a-input-number v-model="form.nums" />
          </a-form-item>
          <a-form-item label="价格" type="number">
            <a-input-number v-model="form.price" />
          </a-form-item>
          <a-form-item label="单位">
            <a-input v-model="form.unit" />
          </a-form-item>
        </a-form>
      </a-spin>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
const getFormDefaultParams = () => ({
  name: '',
  introduction: '',
  category_id: undefined,
  nums: 0,
  price: 0,
  unit: '',
  cover_img: '',
  detail_img: ''
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '名字',
        dataIndex: 'name'
      },
      {
        title: '介绍',
        dataIndex: 'introduction'
      },
      {
        title: '所属分类',
        dataIndex: 'category_name'
      },
      {
        title: '数量',
        dataIndex: 'nums'
      },
      {
        title: '价格',
        dataIndex: 'price'
      },
      {
        title: '单位',
        dataIndex: 'unit'
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      productVisible: false,
      isEdit: false,
      submitLoading: false,
      modalLoading: false,
      form: getFormDefaultParams(),
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
      imgPrefix: process.env.VUE_APP_IMG_PREFIX,
      uploadPrefix: process.env.VUE_APP_UPLOAD_PREFIX,
      coverLoading: false,
      detailLoading: false,
      loadData: paramters => {
        return this.getProductList(
          Object.assign({}, paramters, {shop_id: sessionStorage.getItem('shop_id') ? Number(sessionStorage.getItem('shop_id')): undefined})
        )
      }
    }
  },
  computed: {
    ...mapState('product', ['detail']),
    ...mapState('category', {
      categoryList: 'list'
    }),
    category() {
      if (this.categoryList.length === 0) return []
      return this.categoryList.map(item => {
        return {
          label: item.category_name,
          value: item.id
        }
      })
    }
  },
  watch: {
    productVisible() {
      if (!this.productVisible) {
        this.form = getFormDefaultParams()
      }
    }
  },
  methods: {
    ...mapActions('product', {
      getProductList: 'getList',
      addProduct: 'add',
      updateProduct: 'update',
      getProductDetail: 'getDetail',
      deleteProduct: 'delete'
    }),
    ...mapActions('category', {
      getCategoryList: 'getList'
    }),
    toEdit(id) {
      this.isEdit = true
      this.modalLoading = true
      this.productVisible = true
      this.getProductDetail(id).then(res => {
        this.form = Object.assign({}, this.form, res.data)
      }).finally(() => {
        this.modalLoading = false
      })
    },
    toDelete(id) {
      const _this = this
      this.$confirm({
        content: '你确定要删除这个产品？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteProduct(id).then(res => {
            _this.$refs.table.refresh(true)
          })
        }
      });
    },
    popProduct() {
      this.isEdit = false
      this.form = getFormDefaultParams()
      this.productVisible = true
    },
    hanldeCoverChange(e) {
      const file = e.file
      if (file.status === 'uploading') {
        this.form.cover_img = ''
        this.coverLoading = true
      }
      if (file.status === 'done') {
        this.form.cover_img = file.response.data
        this.coverLoading = false
      }
    },
    hanldeDetailChange(e) {
      const file = e.file
      if (file.status === 'uploading') {
        this.form.detail_img = ''
        this.detailLoading = true
      }
      if (file.status === 'done') {
        this.form.detail_img = file.response.data
        this.detailLoading = false
      }
    },
    handleSubmit() {
      if (this.modalLoading) {
        this.productVisible = false
        return ;
      }
      this.form.shop_id = Number(sessionStorage.getItem('shop_id'))
      if (!this.form.shop_id) {
        this.$message.error('商户id丢失')
        return ;
      }
      this.submitLoading = true
      if (this.isEdit) {
        this.updateProduct({
          id: this.form.id,
          data: this.form
        }).then(() => {
          this.$message.success('修改成功')
          this.$refs.table.refresh(true)
          this.productVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      } else {
        this.addProduct(this.form).then(() => {
          this.$message.success('添加成功')
          this.$refs.table.refresh(true)
          this.productVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      }
    }
  },
  created() {
    this.getCategoryList({limit: -1})
  }
}
</script>
<style lang="scss" scoped>
/deep/.ant-modal {
  /deep/.ant-form-item {
    margin-bottom: 0;
  }
}
</style>